<template>
    <div class="detail">
        <div class="dt_title" v-show="isShow">
            <h4>{{detailData.title}}</h4>
            <p><span>标签：{{detailData.classify_tagname}}</span><span>{{detailData.publish_time}}</span></p>
        </div>
        <div class="dt_content">
            <div v-html="detailData.content"></div>
        </div>
    </div>
</template>
<script>
export default {
    name:'admin_edit',
    data(){
        return{
            detailData:'',
            isShow: false,
        }
    },
    created(){
        
    },
    mounted(){
        this.getData();
    },
    activated(){
        this.getData();
    },
    deactivated(){
        // 页面离开
        this.isShow = false;
    },
    methods:{
        getData:function(){
            var _this = this;
            this.requests({reqUrl:'/api/index/get_detail?id='+this.$route.query.id}).then(function(ret){
                console.log(ret)
                if(ret){
                    _this.detailData = ret.detail[0];
                }
                _this.isShow = true;
            })
        }
    }
}
</script>
<style>
    .detail{
        padding: 20px 15px;
    }
    .dt_title{
        text-align: center;
        border-bottom: 1px solid rgb(200, 200, 200);
        padding-bottom: 20px;
        /* margin-bottom: 20px; */
    }
    .dt_title h4{
        font-weight: normal;
        margin-bottom: 10px;
    }
    .dt_title p{
        color: rgb(112, 112, 112);
        font-size: 12px;
    }
    .dt_title p span:first-of-type{
        display: inline-block;
        margin-right: 30px;
    }
    .dt_content{
        margin-top: 30px;
    }
    .dt_content *{
        word-wrap: break-word; 
        word-break: normal; 
        white-space: pre-wrap;
    }
    .dt_content code{
        display: block;
        background-color: #f1f1f1;
        border-radius: 3px;
        padding: 10px;
    }

    
</style>


